@import "https://fonts.googleapis.com/css2?family=Alkatra&display=swap";
@import "https://fonts.googleapis.com/css2?family=Fira+Sans&display=swap";
:root {
  --white: beige;
  --blue: #237798;
  --dark-blue: #19566d;
  --small-font: 1.5rem;
  --medium-font: 1.8rem;
  --big-font: 2rem;
  --gap: 2rem;
  --margin: 2rem;
}

*, :after, :before {
  box-sizing: border-box;
}

body {
  color: var(--white);
  background: #000;
  margin: 0;
  font-family: Fira Sans, sans-serif;
  font-size: 16px;
}

.hidden {
  opacity: 0;
  filter: blur(5px);
  transition: all 1s;
  transform: translateX(-20%);
}

.show {
  opacity: 1;
  filter: blur();
  transform: translateX(0);
}

.btn {
  background-color: var(--dark-blue);
  color: #fff;
  cursor: pointer;
  text-emphasis: 5px;
  border: none;
  border-radius: 50%;
  outline: none;
  padding: 10px;
  display: none;
  position: fixed;
  bottom: 20px;
  right: 20px;
}

.btn:hover {
  background-color: var(--dark-blue);
}

.scroll {
  width: 100%;
  justify-content: center;
  align-items: center;
  display: flex;
}

.scroll-indicator {
  margin: var(--margin) 0;
  width: 24px;
  height: 24px;
  cursor: pointer;
  border: 2px solid #fff;
  border-radius: 50%;
  justify-content: center;
  align-items: center;
  animation: 1s infinite scrollIndicatorAnim;
  display: flex;
}

.scroll-indicator:before {
  content: "";
  width: 8px;
  height: 8px;
  border-bottom: 2px solid #fff;
  border-left: 2px solid #fff;
  justify-content: center;
  align-items: center;
  margin-top: .2rem;
  margin-left: .5rem;
  animation: 1s infinite scrollIndicatorBounce;
  display: flex;
  top: 8px;
  left: 50%;
  transform: rotate(-45deg);
}

@keyframes scrollIndicatorAnim {
  0%, 100% {
    opacity: 0;
  }

  50% {
    opacity: 1;
  }
}

@keyframes scrollIndicatorBounce {
  0%, 100% {
    transform: translate(-50%, -50%)rotate(-45deg)scaleY(1);
  }

  50% {
    transform: translate(-50%, -50%)rotate(-45deg)scaleY(1.2);
  }
}

.header {
  width: 100%;
  z-index: 9999;
  background-color: #000;
  justify-content: space-between;
  align-items: center;
  margin: 0;
  padding: 0 10px;
  transition: top .5s ease-in-out;
  display: flex;
  position: fixed;
  top: -80px;
  left: 0;
}

.header.show-header {
  top: 0;
}

.header__nav {
  text-align: center;
  flex-direction: row;
  gap: 1.2em;
  padding: 0;
  list-style: none;
  display: flex;
}

.header__nav a {
  color: inherit;
  font-size: var(--small-font);
  text-decoration: none;
}

.header__nav li:hover {
  border-bottom: 5px solid var(--blue);
  color: var(--dark-blue);
  padding-bottom: 2px;
  transition: all 0s ease-in-out .5s;
}

.header__button {
  border: 3px solid var(--white);
  padding: 10px;
}

.header__button a {
  color: inherit;
  font-size: var(--small-font);
  text-decoration: none;
}

.header__button:hover {
  color: #000;
  background-color: var(--dark-blue);
  transition: all 0s ease-in-out .5s;
}

.hero {
  z-index: 0;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  padding-top: 100px;
  display: flex;
  overflow: hidden;
}

.hero__image {
  border: 5px solid var(--blue);
  border-radius: 50%;
  justify-content: center;
  align-items: center;
  margin-top: 20px;
  display: flex;
}

.main__image {
  min-height: 200px;
  min-width: 200px;
  border-radius: 50%;
}

.text-contaner {
  margin: var(--margin);
  padding: 0;
  display: flex;
}

.hero__text.one {
  font-size: var(--medium-font);
  text-align: center;
  margin: 0;
  padding: 0;
  font-family: Alkatra, cursive;
}

.hero__text.two {
  margin: 2rem;
  padding: 0;
  font-size: 3rem;
}

.hero__text.three {
  margin: var(--margin);
  padding: 0;
  font-size: 1.5rem;
}

.about {
  margin-bottom: var(--margin);
  flex-direction: column;
  justify-content: center;
  align-items: center;
  display: flex;
}

.about__line {
  width: 100%;
  text-align: center;
  justify-content: center;
  align-items: center;
  display: flex;
}

.about__header {
  font-size: var(--big-font);
}

.about__header.blue {
  color: var(--blue);
  font-family: Alkatra, cursive;
}

hr {
  width: 50%;
  border: 5px solid var(--blue);
}

.about__container {
  gap: var(--gap);
  padding: 0 20px;
  display: flex;
}

.about__image {
  border: 3px solid var(--blue);
  height: 500px;
  width: 410px;
  justify-content: center;
  align-items: center;
  display: flex;
}

.aboutme__image {
  object-fit: contain;
}

.about__text p {
  line-height: 40px;
  font-size: var(--small-font);
  text-align: justify;
  margin-top: 0;
  font-family: Alkatra, cursive;
}

.about__header.two {
  color: var(--blue);
  font-family: Alkatra, cursive;
}

.about__skills {
  display: flex;
}

.skill:nth-child(2) {
  transition-delay: .2s;
}

.skill:nth-child(3) {
  transition-delay: .4s;
}

.skill:nth-child(4) {
  transition-delay: .6s;
}

.skill:nth-child(5) {
  transition-delay: .8s;
}

.skill:nth-child(6) {
  transition-delay: 1s;
}

.about__line.one hr {
  width: 50%;
  border: 5px solid var(--white);
}

.projects {
  margin-bottom: var(--margin);
  flex-direction: column;
  justify-content: center;
  align-items: center;
  display: flex;
}

.projects__container {
  gap: var(--gap);
  flex-wrap: wrap;
  justify-content: center;
  align-items: center;
  display: flex;
}

.projects__image {
  background: var(--blue);
  border-bottom-right-radius: 10px;
  border-bottom-left-radius: 10px;
}

.projects__image a {
  color: inherit;
  text-align: center;
  text-decoration: none;
  list-style: none;
}

.projects__image p {
  text-align: center;
  color: var(--white);
}

#Contact {
  gap: var(--gap);
  margin-bottom: var(--margin);
  flex-direction: column;
  display: flex;
}

.contact__text {
  text-align: center;
  font-size: var(--medium-font);
  font-family: Alkatra, cursive;
}

.contact__title {
  text-align: center;
}

form.form {
  width: 50%;
  border: 1px solid var(--blue);
  box-sizing: border-box;
  margin: 0 auto;
  padding: 20px;
}

form.form input.input, form.form textarea.input {
  width: 100%;
  box-sizing: border-box;
  border: 1px solid #ccc;
  border-radius: 4px;
  margin: 8px 0;
  padding: 12px 20px;
}

form.form input[type="submit"].submit {
  width: 100%;
  background-color: var(--blue);
  color: #fff;
  cursor: pointer;
  border: none;
  border-radius: 4px;
  margin: 8px 0;
  padding: 14px 20px;
}

form.form input[type="submit"].submit:hover {
  background-color: var(--dark-blue);
}

